<template>
  <div>
    <dl>
      <dt>name: </dt>
      <dd>{{ fetchedUser.id }}</dd>
    </dl>
    <dl>
      <dt>karma: </dt>
      <dd>{{ fetchedUser.karma }}</dd>
    </dl>
    <dl>
      <dt>created: </dt>
      <dd>{{ fetchedUser.created }}</dd>
    </dl>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters({
      fetchedUser: "fetchedUser",
    })
  },
  created() {
    const userName = this.$route.params.id;
    this.$store.dispatch("FETCH_USER", userName);
  }
}
</script>

<style scoped>
  dl{display: flex; padding-bottom: 8px;}
  dl dt{width: 70px;}
</style>
