import Vue from "vue"
import Vuex from "vuex";
import { fetchNewsList, fetchAskList, fetchJobsList, fetchUserInfo, fetchItemInfo } from "../api/api.js"

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    news: [],
    ask: [],
    jobs: [],
    user: [],
    items: [],
  },
  getters: {
    fetchedNews(state) {
      return state.news;
    },
    fetchedAsk(state) {
      return state.ask;
    },
    fetchedJobs(state) {
      return state.jobs;
    },
    fetchedUser(state) {
      return state.user;
    },
  },
  mutations: {
    SET_NEWS(state, data) {
      state.news = data;
    },
    SET_ASK(state, data) {
      state.ask = data;
    },
    SET_JOBS(state, data) {
      state.jobs = data;
    },
    SET_USER(state, data) {
      state.user = data;
    },
    SET_ITEM(state, data) {
      state.items = data;
    }
  },
  actions: {
    FETCH_NEWS(context) {
      fetchNewsList()
        .then(response => {
          context.commit("SET_NEWS", response.data)
        })
        .catch(e => console.log(e));
    },
    FETCH_ASK(context) {
      fetchAskList()
        .then(response => {
          context.commit("SET_ASK", response.data);
        })
        .catch(e => console.log(e));
    },
    FETCH_JOBS({ commit }) {
      fetchJobsList()
        .then(({ data }) => {
          commit("SET_JOBS", data);
        })
        .catch(e => console.log(e));
    },
    FETCH_USER(context, name) {
      fetchUserInfo(name)
        .then(response => {
          context.commit("SET_USER", response.data);
        })
        .catch(e => console.log(e));
    },
    FETCH_ITEM({ commit }, id) {
      fetchItemInfo(id)
        .then(({ data }) => {
          commit("SET_ITEM", data);
        })
        .catch(e => console.log(e));
    }
  }
})


export default store;
