<template>
  <div>
    <ul class="news-list" v-if="isPage == 'news'">
      <li v-for="item in fetchedNews" :key="item.id" class="post">
        <!-- 포인트 영역 -->
        <div class="points">{{ item.points }}</div>
        <div>
          <p class="news-title">
            <a :href="item.url" target="_blank">{{ item.title }}</a>
          </p>
          <small class="link-text">
            {{ item.time_ago }} by 
            <router-link :to="`/user/${item.user}`" class="link-text">{{ item.user }}</router-link>
          </small>
        </div>
      </li>
    </ul>
    <ul class="news-list" v-else>
      <li v-for="item in fetchedAsk" :key="item.id" class="post">
        <!-- 포인트 영역 -->
        <div class="points">{{ item.points }}</div>        
        <div>
          <p class="news-title">
            <router-link :to="`/item/${item.id}`">{{ item.title }}</router-link>
          </p>
          <small class="link-text">
            {{ item.time_ago }} by 
            <router-link :to="`/user/${item.user}`" class="link-text">{{ item.user }}</router-link>
          </small>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["fetchedNews", "fetchedAsk"])
  },
  data() {
    return {
      isPage: "news",
    }
  },
  created() {
    if(this.$route.fullPath == "/news") {
      this.isPage = "news";
      this.$store.dispatch("FETCH_NEWS");
    } else if(this.$route.fullPath == "/ask") {
      this.isPage = "ask";
      this.$store.dispatch("FETCH_ASK");
    }
    
  }
}
</script>

<style scoped>
  .post{display: flex; align-items: center; border-bottom: 1px solid #ddd;}
  .post .points{width: 80px; height: 80px; display: flex; align-items: center; justify-content: center; color: #42b883;}
  .link-text{color: #828282;}
</style>
